.App{
  text-align: center;
  align-items: center;
  justify-content: center;
}
.Container{
 display: grid;
 grid-template-columns: repeat(2,1fr);
 margin: 100px;
}
.suit{
  padding: 28px;;
  font-size: xx-large;
}
.card{
  width: 100px;
  height: 150px;
  border: 2px solid rgb(202, 10, 10);
  display: inline-flex;
  margin: 5px;
 

  font-size: larger;
  background-color: white;
}

.deck{
  width:650px;
  background-color:green;
}
.Drwan{
  width:650px;
  background-color: green;
}
.button{
  border: 1px solid rgb(42, 42, 43);
  background-color: aqua;
  border-radius: 5px;
  color:black;
  width:100px;
  height:50px;
  font-size: 20px;
  margin:10px;
}